.softBackground {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.7;
  z-index: 1;
}

.create-task {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 450px;
  height: max-content;
  padding: 25px 35px;
  border-radius: 15px;
  z-index: 1;
}

.title-btn-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.close-btn {
  background-color: transparent;
  border: 0;
}

.input-container {
  width: max-content;
  height: max-content;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.input-container div {
  position: relative;
  display: flex;
  flex-direction: column;
}

input[type=text] {
  padding: 10px;
  height: 10px;
  border: 1px solid black;
  border-radius: 5px;
}

.input-container .counter {
  position: absolute;
  bottom: 0;
  right: 0;
  background-color: transparent;
  border: 0;
  padding: 0 4px 5px;
  color: black;
}

.textarea {
  width: 400px;
  height: 200px;
  padding: 10px;
  resize: none;
  border: 1px solid black;
  border-radius: 5px;
}

input[type=datetime-local] {
  width: 200px;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid black;
  padding: 5px;
}

.input-container button {
  margin: 20px auto;
  width: 150px;
  padding: 10px;
  border: 0;
  border-radius: 10px;
}